<template>
  <div class="login-container">
    <form :onsubmit="loginHandler" action="#">
      <span v-if="errorText" class="error">{{ errorText }}</span>
      <div class="username">
        <input v-model="usernameInput" type="text" placeholder="">
        <div class="label">
          <label for="username">نام کاربری</label>
        </div>
      </div>
      <div class="password">
        <input v-model="passwordInput" type="text" placeholder="">
        <div class="label">
          <label for="username">رمز عبور</label>
        </div>
      </div>
      <a href="#">رمز عبور را فراموش کرده اید؟</a>
      <input type="submit" value="تایید">
    </form>
  </div>
</template>

<script>
import { ref } from 'vue';
import db from "./../database.js";
export default {
    name: "Login",
    setup(){
      const usernameInput = ref("");
      const passwordInput = ref("");
      const errorText = ref("");
      
      function loginHandler(){

        let isValid = db.find((user)=> user.username === usernameInput.value && user.password === passwordInput);

        if (isValid){
          errorText.value = "با موفقیت"
        }else{
          errorText.value += "نام کاربری یا رمز عبور اشتباه است.";
        }

        // if (!usernameInput.value){
        //   errorText.value += "نام کاربری اشتباه است";
        // }else if(!passwordInput.value){
        //   errorText.value += "رمز عبور اشتباه است"
        // }else{
        //   errorText.value = "با موفقیت"
        // }
      }
      

      return {usernameInput, passwordInput, errorText, loginHandler}
    }
}
</script>

<style scoped>
  form {
    display: flex;
    width: 300px;
    padding-top: 50px;
    background-color: var(--color1);
    border-radius: 24px;
    box-shadow: 0 0 20px -1px rgba(0, 0, 0, 0.25);
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 20px;
  }
  form .error {
    background-color: rgba(255, 0, 0, 0.2);
    width: 100%;
    text-align: center;
    color: #000;
    padding: 10px;
  }
  form .username, .password{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  form input:not(input[type="submit"]){
    width: 90%;
    padding: 5px;
    direction: rtl;
    outline: none;
    border: 1px solid #ccc;
    border-radius:  12px;
    background: transparent;
    z-index: 1;
  }
  form div{
    position: relative;
  }
  form div .label{
    position: absolute;
    top: 5px;
    right: 25px;
    font-size: 14px;
    background: var(--color1);
    transition: .2s;
    padding: 0 8px;
    user-select: none;
    z-index: 0;
  }
  form div input:focus + .label{
    font-size: 10px;
    top: -8px;
    color: var(--color3);
    transition: .2s;
  }
  form div input:focus{
    border: 1px solid var(--color3);
    color: var(--color3);
    z-index: 0;
  }

  form input[type="submit"]{
    width: 100%;
    border: none;
    outline: none;
    padding: 8px;
    border-radius: 0 0 24px 24px;
    background: var(--color3);
    color: #fff;
    font-weight: bold;
    cursor: pointer;
    box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.25);
  }
  form a{
    text-decoration: none;
    color: var(--color3);
    font-size: 14px;
  }
</style>