<template>
    <div v-show="doesSearchOpen" class="searchScreen">
        <div @click="closeHandler" class="closer"></div>
        <form action="#">
            <input type="text" placeholder="جستجو...">
            <input type="submit" value="جستجو">
        </form>
    </div>
</template>

<script>
import {provide, ref} from "vue";
export default {
    name: "SearchScreen",
    setup(){
        const doesSearchOpen = ref(false);

        function closeHandler(){
            doesSearchOpen.value = false;
        }

        provide("doesSearchOpen", doesSearchOpen)

        return { doesSearchOpen, closeHandler };
    }
}
</script>

<style scoped>
    .searchScreen{
        position: fixed;
        top: 0;
        left: 0;
        width: 100vw;
        height: 100vh;
        background-color: rgba(0, 0, 0, 0.7);
        backdrop-filter: blur(11.7px);
        z-index: 99999;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .closer{
        position: fixed;
        top: 0;
        left: 0;
        width: 100vw;
        height: 100vh;
        z-index: 20;
    }

    .searchScreen form{
        display: flex;
        justify-content: center;
        align-items: center;
        height: 40%;
        width: 250px;
        position: relative;
        z-index: 50;
    }
    form input[type="text"]{
        width: 250px;
        padding: 4px 10px;
        border-radius: 8px;
        direction: rtl;
        border: none;
        outline: none;
    }
    form input[type="submit"]{
        position: absolute;
        left: 0%;
        top: 50%;
        transform: translate(0%,-50%);
        width: 60px;
        padding: 4px 0;
        border-radius: 8px 0 0 8px;
        outline: none;
        border: none;
        background-color: var(--color4);
        color: #fff;
        cursor: pointer;
        transition-duration: .2s;
        transition-property: width;
        box-shadow: 3px 0 8px 0 var(--color3);
    }
    form input[type="submit"]:hover{
        width: 70px;
        transition-duration: .2s;
        transition-property: width;
    }
</style>