<script>
    import {ref} from "vue";
    import { useScrollLock } from "@vueuse/core"
    export default {
        name: "Nav",
        setup() {
            const showMobileNav = ref(false);

            function openMenu(){
                showMobileNav.value = true;
                isLocked.value = true;
            };

            function closeMenu(){
                showMobileNav.value = false;
                isLocked.value = false;
            };
            const el = document.querySelector("body");
            const isLocked = useScrollLock(el);

            return {showMobileNav, openMenu, closeMenu};
        }
    }
</script>

<style>
    /* Mobile Header */
    .mobile-logo{
        display: none;
    }

    div.nav_mobile .list_nav_mobile {
        display: none;
    }

    .nav_mobile {
        display: none;
        z-index: 999999;
    }

    .nav-container {
        max-width: 1000px;
        width: 100%;
        height: 34px;
        max-height: 34px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        direction: rtl;
        margin-top: 20px;
    }

    .nav-container .links {
        /* width: 484px; */
        height: 100%;
        margin-right: -80px;
    }

    .nav-container .links ul {
        display: flex;
        gap: 20px;
        list-style-type: none;
        justify-content: center;
        align-items: center;
        height: 100%;
    }

    .nav-container .links ul li {
        align-items: center;
    }

    .nav-container .links ul li a {
        color: #000;
        text-decoration: none;
        font-size: 16px;
        padding: 5px;
        border-radius: 8px;
        transition: .3s;
    }

    .nav-container .links ul li.active a,
    .nav-container .links li:hover a {
        color: var(--color1);
        background-color: var(--color4);
        border-radius: 8px;
        transition: .3s;
    }

    .nav-container .buttons {
        display: flex;
        flex-direction: row-reverse;
        justify-content: center;
        align-items: center;
        gap: 20px;
    }

    .nav-container .buttons .login {
        height: 34px;
        background-color: var(--color4);
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 14px;
        padding: 5px;
        border-radius: 8px;
        transition-duration: .3s;
        transition-property: scale;
    }

    .nav-container .buttons .login:hover {
        scale: 1.1;
        transition-duration: .3s;
        transition-property: scale;
    }

    .nav-container .buttons .login a {
        display: flex;
        justify-content: center;
        align-items: center;
        color: var(--color1);
        gap: 5px;
        text-decoration: none;
    }

    .nav-container .buttons .searchBar form {
        width: 180px;
        height: 34px;
        position: relative;
    }

    .nav-container .buttons .searchBar form input {
        width: 100%;
        height: 100%;
        font-size: 14px;
        border-radius: 8px;
        padding-right: 5px;
        outline: none;
        border: none;
        background: transparent;
        box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.25);
        transition-duration: .3s;
        transition-property: scale;
    }

    .nav-container .buttons .searchBar form input:focus {
        scale: 1.05;
        transition-duration: .3s;
        transition-property: scale;
    }

    .nav-container .buttons .searchBar form svg {
        cursor: pointer;
        position: absolute;
        left: 5px;
        top: 50%;
        transform: translate(0%, -50%);
    }

    .mobile-logo img{
        width: 34px;
    }

    /* Respawnsive */
    @media (max-width: 992px) {
        .nav-container .buttons .searchBar form input {
            display: none;
        }

        .nav-container .buttons .searchBar form svg {
            background-color: transparent;
            border: 1px solid var(--color4);
            padding: 8px;
            border-radius: 12px;
            box-sizing: unset;
        }

        .nav-container .links {
            margin-right: 20px;
        }

        .nav-container .links ul li a {
            font-size: 14px;
        }

        .nav-container .links ul {
            gap: 10px;
        }
    }

    @media (max-width: 827px) {
        .nav-container {
            padding: 0 40px;
        }

        .nav-container .links {
            display: none;
        }
    }

    @media (max-width: 768px) {

        .nav_mobile{
            display: block;
        }

        .nav-container{
            display: flex;
            flex-direction: row-reverse;
        }

        .nav-container .buttons .searchBar {
            display: none;
        }

        /* Mobile Navbar */
        div.nav_mobile {
            padding-bottom: 8px;
            height: 100vh;
            padding-right: 20px;
            z-index: 9999;
        }

        .list_nav_mobile .close {
            width: 100%;
            height: 32px;
            display: flex;
            justify-content: end;
            align-content: center;
        }
        .close svg{
            width: 32px;
        }

        .nav_mobile .label_nav_mobile{
            position: absolute;
            top: 0;
            left: 30px;
            width: 34px;
            height: 34px;
        }
        .nav_mobile .label_nav_mobile svg{
            width: 34px;
            height: 34px;
        }

        div.nav_mobile .list_nav_mobile {
            transition: all .25s ease-out;
            display: flex;
            flex-direction: column;
            gap: 10px;
            list-style-type: none;
            padding: 10px;
            position: fixed;
            left: 0;
            background-color: rgba(255, 255, 255, .7) !important;backdrop-filter: blur(11.7px);
            height: 100vh;
            top: -20px;
            width: 80%;
            z-index: 99999;
        }

        div.nav_mobile .list_nav_mobile li a {
            color: var(--color4);
            display: flex;
            flex-direction: row-reverse;
            justify-content: center;
            align-items: center;
            gap: 8px;
            font-weight: bold;
            font-size: 18px;
            text-decoration: none;
        }

